.terminal-container {
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    font-family: 'Roboto Mono', monospace;
    border-radius: 7px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
  
.window-container {
    width: 100%;
    height: auto;
    border: 1px solid #bbb;
    background: #1e1f29;
    border-radius: 7px;
    display: flex;
    flex-direction: column;
    overflow: hidden;
}

.window-controls {
    display: flex;
    padding: 5px;
    background: linear-gradient(rgb(226, 224, 226), rgb(199, 198, 200));
}

.window-description {
    display: flex;
    margin: auto;
    justify-self: center;
}

.window-controls-btn {
    position: relative;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    border: 1px solid rgba(0, 0, 0, 0.1);
    padding: 0;
}

.close-btn {
    margin: 3.5px;
    background: rgb(227, 69, 69);
}

.minimize-btn {
    margin: 3.5px;
    background: rgb(224, 178, 40);
}

.expand-btn {
    margin: 3.5px;
    background: #42c933;
}

.window-content {
    display: flex;
    padding: 10px;
    flex-direction: column;
    flex: 0 0 95%;
    background: #1e1f29;
    color: #fff;
}

.line {
    color: #fff;
    margin: 0;
    padding: 3px 5px;
}